import { GSP, Package, PackageModule } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector, Type } from '@angular/core';
import { CmpDesignerPackageView } from './views';
import { CmpDesignerFrameComponent } from './views/cmp-designer-frame/cmp-designer-frame.component';

export class CmpDesignerPackage extends Package {

  private layoutPanel: any;

  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  activate(state) {
    this.gsp.workspace.addOpener((uri: string) => this.openCmp(uri));
  }
  /**
   * 打开构件设计器
   * @param uri URI路径
   */
  openCmp(uri) {
    if (uri.endsWith('.beCmp') || uri.endsWith('.beMgrCmp') || uri.endsWith('.beDtmCmp')
      || uri.endsWith('.beValCmp') || uri.endsWith('.vmCmp') || uri.endsWith('.udtValCmp') || uri.endsWith('.udtDtmCmp')) {
      const pkgView = new CmpDesignerPackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as CmpDesignerFrameComponent;
      currentElement.metadataUri = uri;
      // 这里需要处理uri，title应返回元数据名称，
      return {
        getTitle: () => currentElement.getFileNameFromUri(uri),
        getElement: () => pkgView.getElement()
      };
    }
  }
}



