import { CompMethod } from '../BaseEntity/CompMethod';
import { VoidReturnType } from '../BaseEntity/VoidReturnType';
import { UDTValidParameter } from './UDTValidParameter';

export class UDTValidMethod extends CompMethod {
    UDTValidParams: Array<UDTValidParameter>;
    public UDTValidMethod() {
        this.ReturnValue = new VoidReturnType();
        this.UDTValidParams = this.Parameters = new Array<UDTValidParameter>();
    }
}



