import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { HttpUrlConst } from './cmp-reference';

// import { HttpUrlConst } from '../util/be-reference';

/**
 * be发布按钮服务
 */
@Injectable()
export class CmpPublishService {

    constructor(private http: Http) {
    }

    currentUrl = HttpUrlConst.currentUrl;

    /**
     * 发布webApi
     */
    publishBizEntity(fileName: string, path: string): Observable<any> {
        const jitData = new JitMetadataContent();
        jitData.name = fileName;
        jitData.path = path;
        // const jitUrl = `${this.currentUrl}` + `/api/dev/bef/v1.0/becodegenerate/publish`;
        const jitUrl = `${this.currentUrl}` + `/api/dev/main/v1.0/udt/publish`;
        return this.http.put(jitUrl, jitData);
    }
}

/**
 * jit实体结构
 */
class JitMetadataContent {
    name: string;
    path: string;
}



