import { NgModule } from '@angular/core';
import { ComponentService } from './service/component.service';
import { ComponentFactoryModule } from './component-factory/component-factory.module';
import { CmpDesignerFrameComponent } from './cmp-designer-frame.component';
import { ButtonsModule, ButtonGroupModule } from '@progress/kendo-angular-buttons';
import { CommonModule } from '@angular/common';

@NgModule({
  declarations: [
    CmpDesignerFrameComponent
  ],
  imports: [
    ComponentFactoryModule, // 自定义Module
    CommonModule,
    ButtonsModule,
    ButtonGroupModule,
  ],
  providers: [ComponentService],
  bootstrap: [CmpDesignerFrameComponent]
})
export class CmpDesignerFrameModule { }



