import { GSPComponent } from '../BaseEntity/GSPComponent';
import { UDTValidMethod } from './UDTValidMethod';

export class UDTValidComponent extends GSPComponent {
    ComponentType = 'UDTValidComponent';
    UDTValidMethod: UDTValidMethod;
    public UDTValidComponent() {
        this.UDTValidMethod = this.Method = new UDTValidMethod();
    }
}



