import { Component, OnInit } from '@angular/core';
import { MetadataDto, GSPMetadataService, HttpUrlConst, DeterminationComponent, ValidationComponent } from './service/cmp-reference';
import { isNullOrUndefined } from 'util';
import { GSPComponent } from './service/libs/GspComponent/src/BaseEntity/GSPComponent';
import { CmpMetadataContent } from './service/libs/GspComponent/src/CmpMetadataContent';
import { CmpTypeConst } from './service/libs/const/src/CmpTypeConst';
import { BEMgrComponent } from './service/libs/GspComponent/src/BEMgrComponent/BEMgrComponent';
import { BEComponent } from './service/libs/GspComponent/src/BEComponent/BEComponent';
import { CmpPublishService } from './service/cmp-publish.service';
import { VMComponent } from './service/libs/GspComponent/src/VMComponent/VMComponent';
import { BEValidation } from './service/libs/GspComponent/src/BEComponent/BEValidation';
import { BEMgrValidation } from './service/libs/GspComponent/src/BEMgrComponent/BEMgrValidation';
import { DtmValidation } from './service/libs/GspComponent/src/DeterminationComponent/DtmValidation';
import { VldValidation } from './service/libs/GspComponent/src/ValidationComponent/VldValidation';
import { VMValidation } from './service/libs/GspComponent/src/VMComponent/VMValidation';
import { UDTValidComponent } from './service/libs/GspComponent/src/UDTValidComponent/UDTValidComponent';
import { CacheService } from '@farris/ide-devkit/services';
import { UDTDtmComponent } from './service/libs/GspComponent/src/UDTDtmComponent/UDTDtmComponent';
import { UDTDtmValidation } from './service/libs/GspComponent/src/UDTDtmComponent/UDTDtmValidation';
import { UDTValidValidation } from './service/libs/GspComponent/src/UDTValidComponent/UDTValidValidation';

@Component({
  selector: 'app-cmp-designer-frame',
  templateUrl: './cmp-designer-frame.component.html',
  styleUrls: ['./cmp-designer-frame.component.css'],
  providers: [GSPMetadataService, CmpPublishService]
})

export class CmpDesignerFrameComponent implements OnInit {
  component: GSPComponent;
  /**
   * 构件元数据信息
   */
  private _metadataContent: MetadataDto;
  cmpMetadataContent: CmpMetadataContent;
  private sessionId = this.cache.get('sessionId');
  /**
   * 异常堆栈窗口是否显示
   */
  showErrorStackWindow = false;
  errorStack: string;
  errorStackHead: string;
  errorToken = '#GSPCmpError#';

  constructor(private lcmService: GSPMetadataService, private cmpPublishService: CmpPublishService, private cache: CacheService, ) { }
  ngOnInit(): void {

  }

  /**
   * 根据传入的uri加载构件元数据
   */
  set metadataUri(uri: string) {
    const fileName = this.getFileNameFromUri(uri);
    const path = this.getPathFromUri(uri);
    this.lcmService.LoadMetadata(fileName, path).subscribe(
      data => {
        this._metadataContent = data;
        this.loadCmp(data);
      });
  }
  /**
   * 截取文件名
   * @param uri
   */
  getFileNameFromUri(uri: string): string {
    const s = uri.split('/');
    return s[s.length - 1];
  }
  /**
   * 截取地址
   * @param uri
   */
  getPathFromUri(uri: string): any {
    return uri.substring(1, uri.lastIndexOf('/'));
  }
  /**
   * Set构件元数据
   */
  set metadataContent(metadataContent: any) {
    this._metadataContent = metadataContent;
    this.loadCmp(metadataContent);
  }

  /**
   * 加载构件元数据信息到构件设计器
   * @param dto 从构件元数据中读取到的构件元数据信息
   */
  loadCmp(dto: MetadataDto): any {
    // 校验
    if (isNullOrUndefined(dto) || isNullOrUndefined(dto.content)) {
      this.clearData();
      return;
    }
    HttpUrlConst.currentPath = dto.relativePath;
    this.cmpMetadataContent = new CmpMetadataContent();
    this.cmpMetadataContent.Content = JSON.parse(dto.content);
    this.cmpMetadataContent.Type = this.cmpMetadataContent.Content.ComponentType;

    switch (this.cmpMetadataContent.Type) {
      case CmpTypeConst.BEMgrCmpType:
        this.component = this.cmpMetadataContent.Content as BEMgrComponent;
        break;
      case CmpTypeConst.BECmpType:
        this.component = this.cmpMetadataContent.Content as BEComponent;
        break;
      case CmpTypeConst.DeterminationCmpType:
        this.component = this.cmpMetadataContent.Content as DeterminationComponent;
        break;
      case CmpTypeConst.ValidationCmpType:
        this.component = this.cmpMetadataContent.Content as ValidationComponent;
        break;
      case CmpTypeConst.VMCmpType:
        this.component = this.cmpMetadataContent.Content as VMComponent;
        break;
      case CmpTypeConst.UDTValidCmpType:
        this.component = this.cmpMetadataContent.Content as UDTValidComponent;
        break;
      case CmpTypeConst.UDTDtmCmpType:
        this.component = this.cmpMetadataContent.Content as UDTDtmComponent;
        break;
      default:
        alert('当前打开的文件不属于BE构件，其错误的BE构件类型：' + this.cmpMetadataContent.Type);
    }
  }

  clearData() {
    this.component = null;
  }
  /**
   * 保存元数据
   */
  saveMetadata() {
    // 保存前校验
    if (!this.validateBeforeSave(this.component)) {
      return;
    }
    console.log('保存的构件元数据如下：');
    console.log(this.component);
    const dto = this._metadataContent;
    this.cmpMetadataContent.Content = this.component;
    dto.content = JSON.stringify(this.cmpMetadataContent.Content);
    this.lcmService.SaveMetadata(dto, this.sessionId).subscribe(saveResponse => {
      // 保存后需要主动再次加载，因为在保存前事件中赋值了构件和dbo相关的信息，通过加载可以获取最新be
      this.lcmService.LoadMetadata(dto.fileName, dto.relativePath).subscribe(
        data => {
          this.loadCmp(data);
          alert('保存成功');
        }
      );
    }, error => {
      const head = '保存失败。';
      const errorString = <string>(error.text ? error.text() : error.text);
      this.handleErrorString(head, errorString);
    });
  }
  validateBeforeSave(saveCmp: GSPComponent): boolean {
    let curCmp;
    switch (saveCmp.ComponentType) {
      case CmpTypeConst.BEMgrCmpType:
        curCmp = saveCmp as BEMgrComponent;
        if (!BEMgrValidation.ValidateBeforeSave(curCmp)) {
          return false;
        }
        break;
      case CmpTypeConst.BECmpType:
        curCmp = saveCmp as BEComponent;
        if (!BEValidation.ValidateBeforeSave(curCmp)) {
          return false;
        }
        break;
      case CmpTypeConst.DeterminationCmpType:
        curCmp = saveCmp as DeterminationComponent;
        if (!DtmValidation.ValidateBeforeSave(curCmp)) {
          return false;
        }
        break;
      case CmpTypeConst.ValidationCmpType:
        curCmp = saveCmp as ValidationComponent;
        if (!VldValidation.ValidateBeforeSave(curCmp)) {
          return false;
        }
        break;
      case CmpTypeConst.VMCmpType:
        curCmp = saveCmp as VMComponent;
        if (!VMValidation.ValidateBeforeSave(curCmp)) {
          return false;
        }
        break;
      case CmpTypeConst.UDTValidCmpType:
        curCmp = saveCmp as UDTValidComponent;
        if (!UDTValidValidation.ValidateBeforeSave(curCmp)) {
          return false;
        }
        break;
      case CmpTypeConst.UDTDtmCmpType:
        curCmp = saveCmp as UDTDtmComponent;
        if (!UDTDtmValidation.ValidateBeforeSave(curCmp)) {
          return false;
        }
        break;
    }
    return true;
  }
  /**
   * 撤销修改
   */
  reloadMetadata() {
    this.lcmService.LoadMetadata(this._metadataContent.fileName, this._metadataContent.relativePath).subscribe(
      data => {
        this.loadCmp(data);
        alert('撤销成功');
      }, error => {
        const head = '撤销失败。';
        const errorString = <string>(error.text ? error.text() : error.text);
        this.handleErrorString(head, errorString);
      }
    );
  }

  publishCmp() {
    this.cmpPublishService.publishBizEntity(this._metadataContent.fileName, this._metadataContent.relativePath).subscribe(data => {
      // 发布后需要主动再次加载，因为在发布时中赋值了构件相关的信息，通过加载可以获取最新udt
      this.lcmService.LoadMetadata(this._metadataContent.fileName, this._metadataContent.relativePath).subscribe(
        loadData => {
          // 因保存并同步后生成cmp元数据，需要刷新文件树
          // this.treeModel.refresh();
          this.loadCmp(loadData);
          alert('发布成功。');
        }
      );
    }, error => {
      const head = '发布失败。';
      const errorString = <string>(error.text ? error.text() : error.text);
      this.handleErrorString(head, errorString);
    });
  }
  //#region 异常堆栈窗口
  /**
   * 显示异常堆栈
   */
  showErrorStack(errorHead: string, errorMessage: string) {
    this.errorStackHead = errorHead;
    this.errorStack = errorMessage;
    this.showErrorStackWindow = !this.showErrorStackWindow;
  }

  /**
   * 异常处理
   * @param head
   * @param errorString
   */
  handleErrorString(head: string, errorString: string) {
    if (!isNullOrUndefined(errorString) && errorString.includes(this.errorToken)) {
      const strings = errorString.split(this.errorToken);
      const errorMessage = strings[1];
      this.showErrorStack(head, errorMessage);
    } else {
      this.showErrorStack(head, errorString);
    }
  }
  //#endregion
}



