import { Component, OnInit, Input, SimpleChanges, OnChanges, OnDestroy } from '@angular/core';
import { ParameterTypes, ParameterType } from '../../service/libs/GspComponent/src/enum/ParameterType';
import { ReturnValue, VoidReturnType, Guid } from '../../service/libs';
import { FormGroup, FormBuilder } from '@angular/forms';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-return-info-card',
  templateUrl: './return-info-card.component.html',
  styleUrls: ['./return-info-card.component.css']
})
/**
 * 组件：返回值卡片
 */
export class ReturnInfoCardComponent implements OnInit, OnChanges, OnDestroy {
  /**
   * 返回值参数类型
   */
  public paramTypes = ParameterTypes;

  /**
   * 返回值类型Index，默认是String类型
   */
  public selectedIndex = 0;
  /**
   * 输入的返回值实体
   */
  @Input() returnValue: ReturnValue;

  public formGroup: FormGroup;

  /**
     * 表单值更新事件订阅
     * 更改类型为Subscription时，报错：类型冲突
     */
  private formGroupSubscriptions: any[];

  ngOnInit() {
    this.load();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.refresh();
  }
  ngOnDestroy(): void {
    this.unsubscribeFormGroupValueChanges();
  }

  constructor(private formBuilder: FormBuilder) {
  }

  /**
   * 加载
   */
  private load(): any {
    this.createForm();
    this.refresh();
  }

  /**
   * 刷新
   */
  refresh() {
    if (isNullOrUndefined(this.returnValue)) {
      this.createNewVoidReturnType();
    }
    this.unsubscribeFormGroupValueChanges();
    this.enableFormGroup();
    this.setFormValue(this.returnValue);
    this.subscribeFormGroupValueChanges();
  }

  private createNewVoidReturnType() {
    let orginId = this.returnValue.ID;
    const voidReturnValue = new VoidReturnType();
    Object.assign(this.returnValue, voidReturnValue);
    if (isNullOrUndefined(orginId)) {
      orginId = Guid.newGuid();
    }
    this.returnValue.ID = orginId;
  }
  private createNewReturnType(): ReturnValue {
    let orginId = this.returnValue.ID;
    const returnVal = new ReturnValue();
    Object.assign(this.returnValue, returnVal);
    if (isNullOrUndefined(orginId)) {
      orginId = Guid.newGuid();
    }
    this.returnValue.ID = orginId;
    const stringType = ParameterType.String;
    this.returnValue.ParameterType = stringType;
    this.returnValue.ClassName = this.getClassName(stringType);
    this.returnValue.Assembly = this.getAssembly(stringType);
    return this.returnValue;
  }
  /**
   * 使表单可编辑
   */
  private enableFormGroup() {
    // this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.enable();
    // this.subscribeFormGroupValueChanges();
  }

  /**
   * 初始化表单
   */
  private createForm() {
    this.formGroup = this.formBuilder.group({
      IsUseReturnType: false,
      Assembly: '',
      ClassName: '',
      ParamDescription: '',
      ParameterType: ''
    });
  }

  /**
   * 表单赋值
   * @param returnValue
   */
  private setFormValue(returnValue: ReturnValue) {
    // this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(returnValue)) {
      this.clearFormValue();
      return;
    }
    // 若为无返回值类型，也需要清空输入值
    if (!this.getIsUseReturnType(returnValue)) {
      this.clearFormValue();
      return;
    }
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.enable();
    if (returnValue.ParameterType !== ParameterType.Custom) {
      this.formGroup.patchValue({
        IsUseReturnType: this.getIsUseReturnType(returnValue),
        Assembly: this.GetParamAssemblyDisplay(returnValue),
        ClassName: this.GetParamClassNameDisplay(returnValue),
        ParamDescription: returnValue.ParamDescription || '',
        ParameterType: isNullOrUndefined(returnValue.ParameterType) ? '' : returnValue.ParameterType
        // Tips:
        // 此处有关参数类型赋值，因为枚举值可能为0,0的时候进行或操作，会被判断为'否'，使用''，导致清空string选项
        // 正确的写法应该如上
      });
    } else {
      this.formGroup.patchValue({
        IsUseReturnType: this.getIsUseReturnType(returnValue),
        Assembly: returnValue.Assembly || '',
        ClassName: returnValue.ClassName || '',
        ParamDescription: returnValue.ParamDescription || '',
        ParameterType: isNullOrUndefined(returnValue.ParameterType) ? '' : returnValue.ParameterType
      });
    }
    // this.subscribeFormGroupValueChanges();
  }
  /**
 * 表单清空
 */
  private clearFormValue() {
    // this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }

    this.formGroup.patchValue({
      IsUseReturnType: false,
      ParameterType: '',
      Assembly: '',
      ClassName: '',
      ParamDescription: '',
    });
    // 相关控件不可编辑
    this.disableReturnInfoControls();
    // this.subscribeFormGroupValueChanges();
  }
  /**
   * 若为[无返回值]，则控件不可编辑
   */
  private disableReturnInfoControls() {
    this.formGroup.controls.ParameterType.disable();
    this.formGroup.controls.Assembly.disable();
    this.formGroup.controls.ClassName.disable();
    this.formGroup.controls.ParamDescription.disable();
  }

  /**
   * 判断是否为空返回值
   * @param returnValue
   */
  getIsUseReturnType(returnValue: ReturnValue): boolean {
    if (isNullOrUndefined(returnValue)) {
      return false;
    }
    if (returnValue.ClassName === 'Void' && returnValue.Assembly === 'mscorlib.dll') {
      return false;
    } else {
      return true;
    }
  }

  /**
   * 参数程序集名
   * @param dataItem
   */
  GetParamAssemblyDisplay(dataItem: ReturnValue): string {
    if (dataItem.ParameterType === ParameterType.Custom) {
      return dataItem.Assembly;
    } else {
      return this.getAssembly(dataItem.ParameterType);
    }
  }

  /**
   * 参数类名
   * @param dataItem
   */
  GetParamClassNameDisplay(dataItem: ReturnValue): string {
    if (dataItem.ParameterType === ParameterType.Custom) {
      return dataItem.ClassName;
    } else {
      return this.getClassName(dataItem.ParameterType);
    }
  }

  getAssembly(type: ParameterType): string {
    const paraType = this.paramTypes.find(item => item.value === type);
    return paraType ? paraType.assembly : '';
  }

  getClassName(type: ParameterType): string {
    const paraType = this.paramTypes.find(item => item.value === type);
    return paraType ? paraType.className : '';
  }

  /**
   * 返回值类型切换事件
   * @param selectedIndex 返回值类型集合中的索引
   */
  selectType({ value }: any) {
    // ④ 更改类型
    this.returnValue.ParameterType = value;
    this.returnValue.Assembly = this.getAssembly(value);
    this.returnValue.ClassName = this.getClassName(value);
    this.refresh();
  }

  /**
   * 判断是否为自定义类型
   * 是否将程序集名/类名输入框置为只读状态
   */
  get isCustomType(): boolean {
    if (isNullOrUndefined(this.returnValue)) {
      return false;
    }
    return this.returnValue.ParameterType === ParameterType.Custom;
  }
  /**
     * 订阅表单值改变事件
     */
  private subscribeFormGroupValueChanges() {
    // ① 返回值是否为void
    const subscriptionIsUseReturnType = this.formGroup.controls.IsUseReturnType.valueChanges.subscribe(
      hasReturnType => {
        if (!hasReturnType) {
          this.createNewVoidReturnType();
        } else {
          this.createNewReturnType();
        }
        this.refresh();
      });
    // ② assembly
    const subscriptionAssembly = this.formGroup.controls.Assembly.valueChanges.subscribe(
      assembly => {
        this.returnValue.Assembly = assembly;
        this.refresh();
      });
    // ③ className
    const subscriptionClassName = this.formGroup.controls.ClassName.valueChanges.subscribe(
      code => {
        this.returnValue.ClassName = code;
        this.refresh();
      });
    // ⑤ description
    const subscriptionDescription = this.formGroup.controls.ParamDescription.valueChanges.subscribe(
      description => {
        this.returnValue.ParamDescription = description;
        this.refresh();
      }
    );
    if (isNullOrUndefined(this.formGroupSubscriptions)) {
      this.formGroupSubscriptions = new Array<any>();
    }
    this.formGroupSubscriptions.push(subscriptionIsUseReturnType);
    this.formGroupSubscriptions.push(subscriptionAssembly);
    this.formGroupSubscriptions.push(subscriptionClassName);
    this.formGroupSubscriptions.push(subscriptionDescription);
  }
  /**
   * 取消订阅表单值改变事件
   */
  private unsubscribeFormGroupValueChanges() {
    if (this.formGroupSubscriptions && this.formGroupSubscriptions.length > 0) {
      while (this.formGroupSubscriptions.length > 0) {
        const subscription = this.formGroupSubscriptions.pop();
        subscription.unsubscribe();
      }
    }
  }
}



