import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { GSPComponent } from '../service/libs';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-component-factory',
  templateUrl: './component-factory.component.html',
  styleUrls: ['./component-factory.component.css']
})
export class ComponentFactoryComponent implements OnInit, OnChanges {
  /**
   * 作用：
   * 构件设计器传入：作为构件实体的载体
   */
  @Input() component: GSPComponent;

  isBe: Boolean;
  isBeMgr: Boolean;
  isDetermination: Boolean;
  isValidation: Boolean;
  isViewModel: Boolean;
  isUdtValid: Boolean;
  isUdtDtm: Boolean;

  constructor() { }

  ngOnInit(): void {
    this.load();
  }

  ngOnChanges(change: SimpleChanges): void {
    // if (isNullOrUndefined(change.component.previousValue) && !isNullOrUndefined(change.component.currentValue)) {
    // 先前没值，现在有值
    this.load();
    // }
    // else {
    //   // 1、先前有值；2、当前无值
    //   this.refresh();
    // }
  }
  load() {
    if (isNullOrUndefined(this.component)) {
      return;
    }
    switch (this.component.ComponentType) {
      case 'BEComponent':
        this.isBe = true;
        break;
      case 'BEMgrComponent':
        this.isBeMgr = true;
        break;
      case 'ValidationComponent':
        this.isValidation = true;
        break;
      case 'DeterminationComponent':
        this.isDetermination = true;
        break;
      case 'VMComponent':
        this.isViewModel = true;
        break;
      case 'UDTValidComponent':
        this.isUdtValid = true;
        break;
      case 'UDTDtmComponent':
        this.isUdtDtm = true;
        break;
      default:
        alert('当前打开的文件不属于BE构件，其错误的BE构件类型为' + this.component.ComponentType + '，请检查！');
        break;
    }
  }
  refresh() {

  }
}



