import { GSPComponent } from '../BaseEntity/GSPComponent';
import { BEMethod } from './BEMethod';

export class BEComponent extends GSPComponent {
    ComponentType = 'BEComponent';
    BEMethod: BEMethod;
    public BEComponent() {
        this.BEMethod = this.Method = new BEMethod();
    }
}



