import { PackageView } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector } from '@angular/core';
import { CmpDesignerFrameModule } from './cmp-designer-frame/cmp-designer-frame.module';
import { CmpDesignerFrameComponent } from './cmp-designer-frame/cmp-designer-frame.component';

export class CmpDesignerPackageView extends PackageView {
  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector) {
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(CmpDesignerFrameComponent);
    const moduleRef = this.injector.get(CmpDesignerFrameModule) as CmpDesignerFrameModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
}



