import { CompMethod } from '../BaseEntity/CompMethod';
import { VoidReturnType } from '../BaseEntity/VoidReturnType';
import { UDTDtmParameter } from './UDTDtmParameter';

export class UDTDtmMethod extends CompMethod {
    UDTDtmParams: Array<UDTDtmParameter>;
    public UDTDtmMethod() {
        this.ReturnValue = new VoidReturnType();
        this.UDTDtmParams = this.Parameters = new Array<UDTDtmParameter>();
    }
}



