import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import {
  GSPComponent, ICompMethod, BEComponent, BEMgrComponent, ValidationComponent,
  DeterminationComponent, VMComponent
} from '../../service/libs';
import { isNullOrUndefined } from 'util';
import { UDTValidComponent } from '../../service/libs/GspComponent/src/UDTValidComponent/UDTValidComponent';
import { UDTDtmComponent } from '../../service/libs/GspComponent/src/UDTDtmComponent/UDTDtmComponent';

@Component({
  selector: 'app-basicinfocard',
  templateUrl: './basicinfocard.component.html',
  styleUrls: ['./basicinfocard.component.css']
})
export class BasicinfocardComponent implements OnChanges {
  @Input() component: GSPComponent;
  cmpMethod: ICompMethod;
  isOnlyCard = false;

  constructor() { }

  ngOnChanges(changes: SimpleChanges): void {
    // if (isNullOrUndefined(changes.component.previousValue) && !isNullOrUndefined(changes.component.currentValue)) {
    //   // 先前没值，现在有值

    // }
    this.load();
  }
  load() {
    switch (this.component.ComponentType) {
      case 'BEComponent':
        this.cmpMethod = (this.component as BEComponent).BEMethod;
        this.isOnlyCard = false;
        break;
      case 'BEMgrComponent':
        this.cmpMethod = (this.component as BEMgrComponent).BEMgrMethod;
        this.isOnlyCard = false;
        break;
      case 'ValidationComponent':
        this.cmpMethod = (this.component as ValidationComponent).ValidationMethod;
        this.isOnlyCard = true;
        break;
      case 'DeterminationComponent':
        this.cmpMethod = (this.component as DeterminationComponent).DeterminationMethod;
        this.isOnlyCard = true;
        break;
      case 'VMComponent':
        this.cmpMethod = (this.component as VMComponent).VMMethod;
        this.isOnlyCard = false;
        break;
      case 'UDTValidComponent':
        this.cmpMethod = (this.component as UDTValidComponent).UDTValidMethod;
        this.isOnlyCard = true;
        break;
      case 'UDTDtmComponent':
        this.cmpMethod = (this.component as UDTDtmComponent).UDTDtmMethod;
        this.isOnlyCard = true;
        break;
      default:
        alert('当前加载构件的构件类型异常，构件类型为：' + this.component.ComponentType + '，请检查！');
        break;
    }
  }
}



