import { GSPComponent } from '../BaseEntity/GSPComponent';
import { VMMethod } from './VMMethod';

export class VMComponent extends GSPComponent {
    ComponentType = 'VMComponent';
    VMMethod: VMMethod;
    public VMComponent() {
        this.VMMethod = this.Method = new VMMethod();
    }
}



